@import "animation.css";

.gallery-wrapper {
  /* position: absolute; */
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 0 1rem;
  display: flex;
  flex-direction: column;
}

.gallery-filter-container {
  position: absolute;
  right: 30px;
  top: 5rem;
  font-family: var(--montserrat);
  font-size: 1.2rem;
  color: #fff;
  padding: 10px 20px;
  border-radius: 10px;
  z-index: 2;
  background-color: rgba(67, 68, 51, 0.8);
  border: 3px solid var(--primary1);
  box-shadow: 0 0 4px 5px gray;
}

.gallery-filter-title {
  padding-top: 0;
  font-size: 0.9rem;
}

#filter-chair {
  padding: 2px;
  border-radius: 5px;
  background-color: var(--accent1-2);
}

.gallery-items-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  overflow: scroll;
}

.name-items-container {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  margin-bottom: 50px;
}

.chair-name {
  display: inline;
  font-family: var(--montserrat);
  font-size: 2.2rem;
  font-weight: 600;
  margin: clamp(0.5rem, 1rem, 2rem);
}

.category-container {
  width: 100%;
  height: 480px;
  display: flex;
  gap: 30px;
  overflow-x: scroll;
  padding: 1em;
  box-sizing: border-box;
}

.category-container::-webkit-scrollbar {
  height: 14px;
}

.category-container::-webkit-scrollbar-track {
  background-color: lightgray;
  border-radius: 5px;
}

.category-container::-webkit-scrollbar-thumb {
  background: var(--accent1-1);
  border-radius: 6px;
  box-shadow: 0 0 14px black;
  transition: background-color 8090ms;
}

.category-container::-webkit-scrollbar-thumb:hover {
  background: var(--accent1-3);
  border-radius: 5px;
  border: 2px solid gray;
}

.category-container::-webkit-scrollbar-button:start {
  display: block;
}

.category-container::-webkit-scrollbar-button:end {
  display: block;
}

.card-container {
  width: max(280px, 320px);
  font-family: var(--lato);
  min-width: 270px;
  max-width: 290px;
  height: 100%;
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 20px 1px lightgray;
  border-radius: 20px;
  overflow: hidden;
  position: relative;
}

.card-image-container {
  width: 100%;
  height: 340px;
  overflow: hidden;
  background-color: var(--primary1);
}

.card-image {
  width: 100%;
  height: 100%;
  aspect-ratio: 1 / 1;
  border-radius: 20px 20px 0 0;
  object-position: center center;
  /* object-fit: cover; */
  box-shadow: 0 1px 5px gray;
  transition: transform 150ms;
  cursor: pointer;
}

.card-image:hover {
  transform: scale(0.9);
}

.description-container {
  padding-top: 2%;
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 32%;
  background-color: hsla(0, 0%, 0%, 0.2);

  /* position: relative; */
}

.left-info-container {
  padding: 5px 0 5px 10px;
  width: 50%;
  display: flex;
  flex-direction: column;
}

.rating-container {
  width: 100%;
  flex-wrap: nowrap;
  height: 26px;
  display: flex;
  align-items: center;
  gap: 4px;
}

.gallery-star-container {
  padding: 0.25rem 0.5rem;
  gap: 2px;
  /* background: var(--accent1-1); */
  background-color: hsla(0, 0%, 0%, 0.5);
  display: flex;
  border-radius: 6px;
  justify-content: space-evenly;
  box-shadow: 0 0px 6px 2px black;
}

.gallery-star-image {
  width: 14px;
  height: 14px;
}

.reviews {
  font-size: 0.8rem;
  margin-left: 8px;
}

.item-name {
  color: var(--accent1-1);
  font-family: var(--rubik);
  font-size: clamp(0.8rem, 1.2vw, 1rem);
  font-weight: 600;
  margin: 10px 0;
  margin-left: 0;
}

.item-category {
  font-size: 0.9rem;
  margin: 5px 0;
}

.right-info-container {
  padding: 5px 10px;
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
  gap: 19%;
}

.price {
  display: flex;
  transform: scaleY(1.2);
  justify-content: flex-start;
  align-items: flex-start;
  font-family: var(--rubik);
  font-weight: 600;
  font-size: 1.4rem;
}

.on-sale-container {
  width: 7.6rem;
  text-align: center;
  background: red;
  color: white;
  border-radius: 5px;
  position: absolute;
  left: 10px;
  top: 40px;
  transform: rotate(-30deg);
}

.on-sale {
  font-size: 1.6rem;
  width: 100%;
}

.details-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: 10px;
  bottom: 14px;
  width: 130px;
  height: 36px;
  padding: 5px 12px;
  background: var(--accent1-1);
  color: var(--primary1);
  font-size: 0.8rem;
  font-weight: 600;
  border: none;
  border-radius: 10px;
  box-shadow: 0 0px 6px 3px gray;
  cursor: pointer;
}

.details-btn:hover {
  transform: scale(1.05);
}

.gallery-right-arrow {
  font-size: 0.9rem;
  color: var(--primary1);
}

@media (max-width: 1220px) {
  .gallery-filter-container {
    padding: 5px 8px;
    width: 140px;
  }

  .gallery-filter-title {
    font-size: 0.8rem;
  }

  #filter-chair {
    font-size: 0.7rem;
    width: 100%;
  }
}

@media (max-width: 420px) {
  .gallery-wrapper {
    width: 100vw;
  }
  .gallery-filter-container {
    width: 100px;
    right: 10px;
    top: 4rem;
  }

  .category-container {
    height: 420px;
  }

  .card-container {
    min-width: 240px;
    max-width: 250px;
  }

  .card-image-container {
  }

  .rating-container {
  }

  .gallery-star-container {
  }

  .gallery-star-image {
    width: 10px;
    height: 10px;
  }

  .reviews {
    font-size: 0.7rem;
  }

  .price {
    font-size: 1.2rem;
  }

  .on-sale-container {
    width: 5.4rem;
    left: 10px;
    top: 28px;
  }

  .on-sale {
    font-size: 1.1rem;
  }

  .item-name {
    font-size: 0.9rem;
  }

  .item-category {
    font-size: 0.8rem;
  }

  .details-btn {
    font-size: 0.7rem;
    padding: 3px 9px;
    height: 32px;
    width: 100px;
  }
}
