@import "var.css";

.curtain {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.header-wrapper,
.footer-wrapper {
  z-index: 10;
}

.aside-content-container {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: calc(100% - 6rem);
}

.aside-container {
  /* position: absolute; */
  width: 180px;
}

.content-container {
  width: calc(100vw - 180px);
  height: calc(100%); /* header's height is 4rem and footer's height is 2rem */
  /* position: absolute; */
  /* top: 4rem;
  left: 180px; */
}

.footer-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 2rem;
}

@media (max-width: 1220px) {
  .aside-container {
    width: 140px;
  }
  .content-container {
    width: calc(100vw - 140px);
  }
}

@media (max-width: 1080px) and (max-height: 450px) {
  .content-container {
    height: 104%;
  }
}

